/**
 * API client for communicating with the SupportMind backend.
 * All requests go through Next.js rewrites → FastAPI.
 */

const BASE = "/api";

async function request<T>(path: string, options?: RequestInit): Promise<T> {
  const res = await fetch(`${BASE}${path}`, {
    headers: { "Content-Type": "application/json", ...options?.headers },
    ...options,
  });

  if (!res.ok) {
    const body = await res.json().catch(() => ({}));
    throw new Error(body.detail || `Request failed: ${res.status}`);
  }

  return res.json();
}

/* ------------------------------------------------------------------ */
/* Dashboard                                                           */
/* ------------------------------------------------------------------ */

export interface DashboardStats {
  total_tickets: number;
  total_conversations: number;
  total_kb_articles: number;
  total_scripts: number;
  total_gaps_detected: number;
  gaps_approved: number;
  gaps_rejected: number;
  gaps_pending: number;
  avg_resolution_tier: number;
}

export function fetchStats(): Promise<DashboardStats> {
  return request("/stats");
}

/* ------------------------------------------------------------------ */
/* Copilot                                                             */
/* ------------------------------------------------------------------ */

export interface SourceDocument {
  id: string;
  doc_type: string;
  title: string;
  snippet: string;
  score: number;
  metadata: Record<string, string>;
}

export interface CopilotResponse {
  answer: string;
  confidence: number;
  sources: SourceDocument[];
  answer_type: string;
}

export function askCopilot(question: string): Promise<CopilotResponse> {
  return request("/copilot/ask", {
    method: "POST",
    body: JSON.stringify({ question }),
  });
}

/* ------------------------------------------------------------------ */
/* Knowledge                                                           */
/* ------------------------------------------------------------------ */

export interface Paginated<T> {
  data: T[];
  meta: { total: number; page: number; page_size: number };
}

export function fetchKBArticles(page = 1, search = ""): Promise<Paginated<Record<string, string>>> {
  const params = new URLSearchParams({ page: String(page), search });
  return request(`/knowledge/articles?${params}`);
}

export interface GraphNode {
  id: string;
  label: string;
  group: string;
  metadata: Record<string, string>;
}

export interface GraphLink {
  source: string;
  target: string;
  relationship: string;
}

export interface KnowledgeGraphData {
  nodes: GraphNode[];
  links: GraphLink[];
}

export function fetchKnowledgeGraph(limit = 80): Promise<KnowledgeGraphData> {
  return request(`/knowledge/graph?limit=${limit}`);
}

/* ------------------------------------------------------------------ */
/* Learning                                                            */
/* ------------------------------------------------------------------ */

export interface LearningEvent {
  event_id: string;
  ticket_number: string;
  conversation_id: string;
  detected_gap: string;
  proposed_kb_id: string;
  draft_summary: string;
  status: string;
  reviewer_role: string;
  timestamp: string;
  review_notes?: string;
  reviewed_at?: string;
}

export function fetchLearningEvents(
  status = "",
  page = 1,
): Promise<Paginated<LearningEvent>> {
  const params = new URLSearchParams({ status, page: String(page) });
  return request(`/learning/events?${params}`);
}

export interface KBDraft {
  title: string;
  body: string;
  tags: string;
  source_ticket: string;
  source_conversation: string;
  source_script: string;
  lineage: { source_type: string; source_id: string; relationship: string }[];
}

export function generateDraft(ticketNumber: string): Promise<{ data: KBDraft }> {
  return request("/learning/generate-draft", {
    method: "POST",
    body: JSON.stringify({ ticket_number: ticketNumber }),
  });
}

export function reviewEvent(
  eventId: string,
  action: "approve" | "reject",
  notes = "",
): Promise<{ data: LearningEvent; message: string }> {
  return request("/learning/review", {
    method: "POST",
    body: JSON.stringify({ event_id: eventId, action, reviewer_notes: notes }),
  });
}

/* ------------------------------------------------------------------ */
/* QA / Compliance                                                     */
/* ------------------------------------------------------------------ */

export function scoreQA(ticketNumber: string): Promise<{ data: Record<string, unknown> }> {
  return request("/qa/score", {
    method: "POST",
    body: JSON.stringify({ ticket_number: ticketNumber }),
  });
}

/* ------------------------------------------------------------------ */
/* Tickets                                                             */
/* ------------------------------------------------------------------ */

export function fetchTickets(
  page = 1,
  search = "",
  status = "",
): Promise<Paginated<Record<string, string>>> {
  const params = new URLSearchParams({ page: String(page), search, status });
  return request(`/tickets?${params}`);
}
